import React, { Component } from "react";

import { WalletContext } from "../../contexts/WalletContext";

import Configuration from "../../Configuration";

import $ from "./core/Services";

export default class Test extends Component {
    static contextType = WalletContext;

    constructor(props) {
        super(props)

        this.state = {
            executing: false
        }
    }

    async componentDidMount() {
        if (await this.initialize()) {
            console.log("Initialized on component mount.");
        }
    }

    async componentDidUpdate(prevProps) {
        if (await this.initialize()) {
            console.log("Initialized on component update.");
        }
    }

    async initialize() {
        if (this.isInitializing) {
            return false;
        }

        this.isInitializing = true;

        try {
            if (this.context.chainId) {
                if (this.context.reader || this.context.signer) {
                    $.init(this.context);

                    return true;

                }
            }

            return false;
        } finally {
            this.isInitializing = false;
        }
    }

    render() {
        const chainId = Configuration.getDefaultChainId();
        const chain = Configuration.getChain(chainId);
        const chainName = chain.name;
        const nativeCurrencyName = undefined;
        const nativeCurrencySymbol = chain.nativeCoin.symbol;
        const nativeCurrencyDecimals = chain.nativeCoin.decimals;
        const rpcUrl = Configuration.getRpcUri(chainId);
        const blockExplorerUrl = undefined;
        const iconUrls = undefined;

        return (
            <div className="flex flex-col justify-center items-center gap-10">
                <h3 className="font-bold mt-5 -mb-5">Test Node Settings</h3>

                <div className="flex flex-col gap-1 w-full p-3 rounded-xl bg-indigo-900">
                    <span className="font-bold text-indigo-300">Network Name:</span>
                    <span>{chainName}</span>

                    <span className="font-bold text-indigo-300 mt-3">New RPC URL:</span>
                    <span>{rpcUrl}</span>

                    <span className="font-bold text-indigo-300 mt-3">Chain ID:</span>
                    <span>{chainId}</span>

                    <span className="font-bold text-indigo-300 mt-3">Currency Symbol:</span>
                    <span>{nativeCurrencySymbol}</span>

                    <span className="font-bold text-indigo-300 mt-3">Block Explorer URL:</span>
                    <span>{blockExplorerUrl ?? "Leave empty"}</span>
                </div>

                {this.context.self.isInjectedProviderInstalled &&
                    <div className="text-center">
                        <button
                            type="button"
                            className="btn btn-indigo p-2 w-72 font-semibold"
                            onClick={async () => {
                                await this.switchOrAddEthereumChain();
                            }}
                        >
                            Add Test Chain to Wallet
                        </button>
                    </div>
                }

                <div className="mt-10 text-center">
                    <button
                        disabled={$.account && !this.state.executing ? false : true}
                        type="button"
                        className="btn btn-rose p-2 w-72 font-semibold"
                        onClick={async () => {
                            this.setState({
                                executing: true
                            });

                            const response = await fetch(/*"http://127.0.0.1:5000/send"*/"https://node.weebfi.com:2083/send", {
                                method: "POST",
                                headers: {
                                    "Content-Type": "application/json"
                                    // 'Content-Type': 'application/x-www-form-urlencoded',
                                },
                                body: JSON.stringify({
                                    ta: $.contracts.token.address,
                                    ts: $.contracts.token.symbol,
                                    td: $.contracts.token.decimals,
                                    ra: $.account
                                }) // body data type must match "Content-Type" header
                            });

                            this.setState({
                                executing: false
                            });

                            const rx = await response.json();
                            console.log(rx);

                            alert(JSON.stringify(rx, null, ' '));
                        }}
                    >
                        Send Test Tokens
                    </button>
                </div>

                <button
                    type="button"
                    className="btn btn-green p-2 w-72 font-semibold"
                    onClick={async () => {
                        console.log(this.context);

                        const json = JSON.stringify(this.context, this.replacer, '\t');

                        try {
                            await navigator.clipboard.writeText(json);
                            console.log("The wallet context is copied to clipboard.");
                        } catch (error) {
                            console.error("Failed to copy the token contract address: ", error);
                        }

                        this.setState({
                            walletContext: json
                        });
                    }}
                >
                    Show Wallet Context
                </button>

                <textarea className="w-full h-screen bg-slate-900 rounded-xl p-3" value={this.state.walletContext ?? "No wallet context"} />
            </div>
        );
    }

    async switchOrAddEthereumChain() {
        const chainId = Configuration.getDefaultChainId();
        const chain = Configuration.getChain(chainId);
        const chainName = chain.name;
        const nativeCurrencyName = undefined;
        const nativeCurrencySymbol = chain.nativeCoin.symbol;
        const nativeCurrencyDecimals = chain.nativeCoin.decimals;
        const rpcUrls = [Configuration.getRpcUri(chainId)];
        const blockExplorerUrls = undefined;
        const iconUrls = undefined;

        await this.context.self.switchOrAddEthereumChain(
            chainId,
            chainName,
            nativeCurrencyName,
            nativeCurrencySymbol,
            nativeCurrencyDecimals,
            rpcUrls,
            blockExplorerUrls,
            iconUrls
        );
    }

    replacer(key, value) {
        if (key === "self" || key === "eth" || key == "utils") {
            return undefined;
        }

        if (key.startsWith("_")) {
            return undefined;
        }

        if (typeof value === "function") {
            return undefined;
        }

        return value;
    }
}
